import React, { useEffect } from 'react';
import { Route, Switch } from 'react-router-dom'
import demo from './pages/demo';
import moment from 'moment'
import 'moment/locale/ru'
import header from './components/header/header';
import mobileNavigation from './components/mobileNavigation/mobileNavigation';
import home from './pages/home';
import requests from './pages/requests/requests';
import peoples from './pages/people/peoples';
import news from './pages/news/news';
import search from './pages/search';
import requestSingle from './pages/requests/requestSingle';

function App() {
  useEffect(() => {
    // set moment locale globally
    moment.locale('ru');
  })
  return (
    <div>
      <header>
        {header()}
      </header>
      <main>
        <Switch>

          <Route exact path="/" component={home} />

          <Route exact path="/requests" component={requests} />
          <Route path="/requests/:id" component={requestSingle} />

          <Route exact path="/people" component={peoples} />

          <Route exact path="/news" component={news} />

          <Route path="/search" component={search} />

          <Route path="/demo" component={demo} />

        </Switch>
      </main>
      <footer></footer>
      {mobileNavigation()}
    </div>
  );
}

export default App;